import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Logo from '../../../common/components/logo/Logo';
import FormInput from '../../../common/components/form/FormInput';
import CallToActionButton from '../../../common/components/callToAction/CallToActionButton';
import Config from '../../../config';
import { parseQueryString } from '../../../utils/url.utils';
import FormAlert from '../../../common/components/form/FormAlert';
import { Link } from 'gatsby';
import styles from './RequestPasswordResetPage.module.css';

const RequestPasswordResetPage: React.FC = (pageContext: any) => {
  const [newPassword, setNewPassword] = useState('');
  const [repeatNewPassword, setRepeatNewPassword] = useState('');
  const [email, setEmail] = useState('');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [showLoginButton, setShowLoginButton] = useState(false);
  const [userId, setUserId] = useState();

  const queryString = parseQueryString(pageContext.location.search);
  useEffect(() => {
    if (queryString.hashedId) {
      setUserId(queryString.hashedId);
    }
  }, []);

  const sendPasswordResetMail = async () => {
    if (!email) {
      success ? setSuccess('') : null;
      setError('Venligst fyll inn epost.');
      return;
    }

    axios
      .post(
        `${Config.apiBaseUrl}/users/current/password/reset`,
        { email },
        {
          headers: { authorization: Config.apiKey },
        }
      )
      .then(response => {
        error ? setError('') : null;
        setSuccess(
          'Du vil om kort tid motta en email med en link du må følge for å tilbakestille passordet ditt.'
        );
      })
      .catch(err => {
        success ? setSuccess('') : null;
        setError(err.response.data.message);
      });
  };

  const changePassword = async () => {
    if (newPassword.length === 0 || repeatNewPassword.length === 0) {
      success ? setSuccess('') : null;
      setError('Venligst fyll inn begge feltene.');
      return;
    }
    if (newPassword !== repeatNewPassword) {
      success ? setSuccess('') : null;
      setError('Begge passordene må være like.');
      return;
    }

    axios
      .post(
        `${Config.apiBaseUrl}/users/current/password/reset/` + userId,
        { new_password: newPassword },
        {
          headers: { authorization: Config.apiKey },
        }
      )
      .then(response => {
        error ? setError('') : null;
        setShowLoginButton(true);
        setSuccess('Passordet ditt er nå oppdatert.');
      })
      .catch(err => {
        success ? setSuccess('') : null;
        setError(err.response.data.message);
      });
  };

  const renderGetResetLinkInMail = () => {
    return (
      <>
        <h3 className={styles.header}>Tilbakestill passord</h3>
        <FormInput
          value={email}
          onChange={evt => setEmail(evt.target.value)}
          placeholder="Epost"
          styleType="secondary"
          className={styles.input}
          required={true}
        />
        <CallToActionButton type="submit" onClick={sendPasswordResetMail}>
          {' '}
          Neste{' '}
        </CallToActionButton>
      </>
    );
  };

  const renderConfirmPasswordChange = () => {
    return (
      <>
        <FormInput
          value={newPassword}
          type="password"
          onChange={evt => setNewPassword(evt.target.value)}
          placeholder="Nytt passord"
          styleType="secondary"
          className={styles.input}
          required={true}
        />
        <FormInput
          value={repeatNewPassword}
          type="password"
          onChange={evt => setRepeatNewPassword(evt.target.value)}
          placeholder="Gjenta passord"
          styleType="secondary"
          className={styles.input}
          required={true}
        />
        <CallToActionButton type="submit" onClick={changePassword}>
          {' '}
          Bekreft{' '}
        </CallToActionButton>
      </>
    );
  };

  return (
    <div className={styles.container}>
      <div className={styles.wrapper}>
        <div className={styles.logo}>
          {' '}
          <Logo />{' '}
        </div>
        {userId ? <> {renderConfirmPasswordChange()} </> : <>{renderGetResetLinkInMail()}</>}
        <div className={styles.alert}>
          {error || success ? <FormAlert error={error} success={success} /> : null}
        </div>
        {showLoginButton && (
          <Link to={'/login'}>
            <CallToActionButton type="button" styleType="secondary">
              {' '}
              Login{' '}
            </CallToActionButton>{' '}
          </Link>
        )}
      </div>
    </div>
  );
};

export default RequestPasswordResetPage;
