import * as React from 'react';
import CallToAction, {
  CallToActionProps,
  CallToActionStyleType,
  CallToActionSize,
} from './CallToAction';

type Props = {} & CallToActionProps & React.ButtonHTMLAttributes<HTMLElement>;

const CallToActionButton: React.FunctionComponent<Props> = ({ ...props }) => {
  return <CallToAction element="button" {...props} />;
};

export type CallToActionProps = CallToActionProps;
export type CallToActionStyleType = CallToActionStyleType;
export type CallToActionSize = CallToActionSize;
export default CallToActionButton;
